body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 2vw;
    font-weight: bold;
    line-height: 34px;
    height: 13vh;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 10vw;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 8% auto;
  }
  
  .game-info {
    margin-left: 20px;
  }

  .game-info button{
    background-color: white;
    border: none;
    box-shadow: 0px 3px 12px #00000017;
    padding: 2%;
    width: 10vw;
    margin: 2%;
    font-size: 1.2vw;
    cursor: pointer;
  }

  .statusDiv{
    font-weight: bold;
    margin: 1% 2%;
    font-size: 2vw;
    width: 16vw;
  }
  
  @media only screen and (max-width: 600px){
    .square {
      font-size: 3vw !important;
      height: 7vh !important;
    }
    .game-info button{
      width: 35vw !important;
      font-size: 3vw;
    }
    .statusDiv{
      width: 45vw !important;
      font-size: 3vw;
    }
  }

  @media only screen and (max-width: 768px){
    .game-info button{
      width: 16vw;
    }
  }

  @media only screen and (max-width: 1024px){
    .game-info button{
      width: 12vw;
    }
  }

  @media only screen and (max-width: 1440px){
    .square {
      font-size: 2vw;
    }

  }